/* eslint-disable */
import Swiper from 'swiper';
import 'swiper/css';

function Carousel(el) {
    const carousel = el.querySelector('.temoignages-carousel');
    const btn_prev = el.querySelector('.btn-prev');
    const btn_next = el.querySelector('.btn-next');

    const swiper = new Swiper(carousel, {
        speed: 400,
        slidesPerView: 1,
        spaceBetween: 10,
        loop: false,
        centeredSlides: false,
        centeredSlidesBounds: false,
        grabCursor: true,
        breakpoints: {
            576: {
                spaceBetween: 10,
                slidesPerView: 1,
            },
            768: {
                spaceBetween: 10,
                slidesPerView: 1,
            },
            992: {
                spaceBetween: 10,
                slidesPerView: 'auto',
            },
            1200: {
                spaceBetween: 10,
                slidesPerView: 'auto',
            },
            1370: {
                spaceBetween: 10,
                slidesPerView: 'auto',
            },
        }
    });

    const setclassbutton = () => {
        if (swiper.isBeginning) {
            btn_prev.classList.add('disabled');
        } else {
            btn_prev.classList.remove('disabled');
        }

        if (swiper.isEnd) {
            btn_next.classList.add('disabled')
        } else {
            btn_next.classList.remove('disabled')
        }
    };

    const next = () => {
        swiper.slideNext();
        setclassbutton();
    };

    const prev = () => {
        swiper.slidePrev();
        setclassbutton();
    };

    btn_prev.addEventListener('click', prev);
    btn_next.addEventListener('click', next);

    setclassbutton();
}

export default Carousel;